@media (max-width: 767px) {
    .message-right,
    .intro-box,
    .side-menu--bottom .contacts,
    .side-menu--bottom .content {
        display: none;
    }
    .theme .app-container header form {
        top: 75px;
        width: calc( 100% - 40px);
        left: 20px;
    }
    .list-item.send-item {
        padding: 8px 16px;
        background-color: transparent;
    }
}

@media (min-width: 769px) {
    .row.messages-list {
        position: relative;
        & * {
            position: static;
        }
        .intro-box {
            background: url(../photos/no-message.svg) no-repeat 98% 98%;
            background-size: 40%;
        }
        .corner-image {
            background: url(../photos/icons/corner.svg) no-repeat 98% 98%;
            background-size: 40%;
        }
        .open-message {
            margin-top: 280px;
            margin-left: 630px;
            top: 687px;
            left: 1429px;
            width: 215px;
            height: 51px;
            text-align: center;
            font: normal normal normal 18px/21px Karla;
            letter-spacing: 0px;
            color: #809BCE;
            opacity: 0.8;
        }
    }
}